        text: ' Tanto  no  os  tinham  que  cheguei  ao  ponto  de  supor  que  eram  as  portas abertas do castelo, e o riso o clarim que chamava os cavaleiros. J a conhecamos, eu  e  o  meu  companheiro  de  escritrio,  o  Joo  Nbrega,  ambos  principiantes  na advocacia,  e  ntimos  como  ningum  mais;  mas  nunca  nos  lembrou  namor-la.  Ela andava ento no galarim; era bela, rica, elegante, e da primeira roda. Mas um dia, no antigo  Teatro  Provisrio  entre  dois  atos  dos  Puritanos,  estando  eu  num  corredor, um  grupo  de  moos  que  falavam  dela,  como  de  uma  fortaleza  inexpugnvel. Dous confessaram haver tentado alguma cousa, mas sem fruto; e todos pasmavam do celibato da moa que lhes parecia sem explicao. E chalaceavam: um dizia que era promessa at ver se engordava primeiro; outro que estava esperando a segunda mocidade do tio para casar com ele; outro que provavelmente encomendara algum anjo ao porteiro do cu; trivialidades que me aborreceram muito, e da parte dos que confessavam t-la cortejado ou amado, achei que era uma grosseria sem nome. No que eles estavam todos de acordo  que ela era extraordinariamente bela; a foram entusiastas e sinceros.',
