            content: 'Pblio Cornlio Cipio, o Africano, que se distinguiu na segunda guerra pnica e venceu Anbal em Zama. Seu neto adotivo Cipio\n' +
              'Emiliano, filho de Paulo Emlio, que foi o segundo de nome Africano, foi o destruidor de Cartago no ano 146 a.C.'
