    authorURL: 'seneca',
    sectionURL: 'sereno-a-seneca',
    content: '1.  Observando-me  atentamente,  descobri  em  mim,    Sneca,  certos  defeitos to visveis que eu os poderia tocar com o dedo; outros que se dissimulam nas  regies  mais  profundas;  outros,  enfim,  que  no  so  contnuos,  mas  que  reaparecem somente a intervalos. Eu no hesito em dizer que estes ltimos so os mais desagradveis de todos. Eles lembram aqueles inimigos que nos rodeiam para nos  assaltar  no  momento  propcio  e  com  os  quais  no  se  sabe  nunca  se  se  deve  estar em p de guerra, ou se se pode contar em um perodo de paz. 2.   No   entanto,   a   disposio   na   qual   eu   me   surpreendo   o   mais   freqentemente  (pois,  por  que  no  me  confessarei  a  ti,  como  a  um  mdico?)    de  no  estar  nem  francamente  liberto  de  minhas  crenas  e  repugnncias  de  outrora,  nem novamente sob seu domnio. Se o estado no qual estou no  o pior que possa existir, , em todo caso, o mais lamentvel e o mais bizarro: no estou nem doente nem so. 3. No me digas que todas as virtudes so fracas nos seus incios e que com o  tempo  elas  tomam  consistncia  e  fora.  Tambm  no  ignoro  que  todos  os  benefcios  que  visam  ao  exterior,  como  o  brilho  de  uma  dignidade,  a  glria  da  1Sneca imagina que o amigo Aneu Sereno (do qual bem pouco conhecemos: a ele o filsofo dedicou trs obras  A Perseverana do Sbio, Da Tranqilidade da Alma, O Lazer  para convert-lo do epicurismo ao estoicismo) lhe apresente algumas perguntas. Na verdade, Sneca no responde exatamente s perguntas e dvidas do amigo, mas de modo geral estas pginas se colocam entre as mais brilhantes e vivazes do escritor romano."\n' +
      'eloqncia  e  todos  aqueles  que  dependem  do  favor  pblico,  tm  necessidade  de  durao  para  crescerem:  as  virtudes  precisam  de  tempo  para  amadurecer,  e  as  qualidades  que  nos  permitem  seduzir  graas  a  um  simples  verniz  esperam  que  o  correr  dos  anos  lhes  acentue  gradualmente  as  cores.  Mas  temo  que  o  hbito  que  fortalece  todas  as  coisas  possa  consolidar  em  mim  esta  fraqueza:  tanto  no  mal  como no bem, um contato prolongado nos faz tomar o gosto. 4.  Em  que  consiste  esta  enfermidade  de  uma  alma  irresoluta,  que  no  se  inclina deliberadamente nem  virtude nem ao vcio? Eu no saberei diz-lo numa palavra,  mas  posso  explic-lo  pormenorizadamente.  Indicar-te-ei  o  que  sinto:  tu  achars o nome da doena. 5. Tenho um profundo amor  simplicidade, e o confesso: o que amo no  nenhum  leito  faustosamente  preparado,  no  so  de  modo  algum  roupas  que  se  tiram  do  fundo  de  um  ba,  que  se  torturam  debaixo  de  pesos  e  de  inmeros  parafusos,  para  obrig-las  a  ficarem  com  lustro,272F2 mas  um  costume  simples  e  grosseiro que se conserva sem cuidado e que se usa sem escrpulo. 6.  O  que  eu  amo  no    uma  alimentao  que  muitos  escravos  preparam  e  olham comer, que se encomenda no sei quantos dias antes e que  apresentada por no sei quantos braos: so pratos fceis de achar e de preparar, que no tm nada de  requintado,  nem  de  raro;  relativamente  aos  quais  se  tem  certeza  de  que  no  faltaro  em  nenhuma  parte,  que  no  pesam  nem    bolsa  nem  ao  estmago  e  que  no tornam a sair de nenhum modo por onde eles entrarem.273F37.  O  que  eu  amo    um  servo  simplesmente  vestido,  um  pequeno  escravo  desajeitado,    a  macia  baixela  de  meu  pai,  homem  rstico,  cuja  argentaria  no  trazia  nem  gravaes,  nem  mesmo  o  nome  do  artfice;    uma  mesa  que  no  deslumbra pelas suas cores marmreas e que no seja conhecida de toda cidade por ter  pertencido  sucessivamente  a  toda  uma  srie  de  amadores,  mas  que  seja  de  uso  cmodo e diante da qual no se vejam os  olhos  dos  convivas  nem  arregalarem  de  prazer nem de cimes',
